import { Component, OnInit } from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { App } from '../../+builder/models/model';
import { AnalyticService } from '../../templates/services/analytic.service';
import { TemplateHttpService } from '../services/templateHttp.service';
import { TemplateRendererService } from '../services/templateRenderer.service';
import { RecommendationService } from '../services/recommendation.service';
import { Script } from '../../../shared/services/index';
import { ThemingService } from '../services/theming.service';

declare var jQuery: any;

@Component({
  selector: 'Temp-dev',
  template: `
        <div [ngSwitch]="Temp_name" [class]="Temp_name">
            <one-page-slider *ngSwitchCase="'one-page-slider'"
               (default_Template)="bind_Template_Json($event)" 
               (selected_control)="jsonBuilderHelper.setSelectedControl($event)"
               (selected_section)="jsonBuilderHelper.setSelectedSection($event)"
               (selected_page)="jsonBuilderHelper.setSelectedPage($event)"
               [JSON_Template]="JSON_Template"
               [devMode]="true"
               class="{{jsonBuilderHelper.getJSONBuilt().themeColor}}"
             >
            </one-page-slider>
            <sound-cloud *ngSwitchCase="'sound-cloud'"
                class="{{jsonBuilderHelper.getJSONBuilt().themeColor}}"
               (default_Template)="bind_Template_Json($event)" 
               (selected_control)="jsonBuilderHelper.setSelectedControl($event)"
               (selected_section)="jsonBuilderHelper.setSelectedSection($event)"
               (selected_page)="jsonBuilderHelper.setSelectedPage($event)"
               [JSON_Template]="JSON_Template"
               [devMode]="true"
            >
            </sound-cloud>
            <one-page-card *ngSwitchCase="'one-page-card'"
                class="{{jsonBuilderHelper.getJSONBuilt().themeColor}}"
               (default_Template)="bind_Template_Json($event)" 
               (selected_control)="jsonBuilderHelper.setSelectedControl($event)"
               (selected_section)="jsonBuilderHelper.setSelectedSection($event)"
               (selected_page)="jsonBuilderHelper.setSelectedPage($event)"
               [JSON_Template]="JSON_Template"
               [devMode]="true"
            >
            </one-page-card>
             <inline-temp *ngSwitchCase="'inline-temp'"
                class="{{jsonBuilderHelper.getJSONBuilt().themeColor}}"
               (default_Template)="bind_Template_Json($event)" 
               (selected_control)="jsonBuilderHelper.setSelectedControl($event)"
               (selected_section)="jsonBuilderHelper.setSelectedSection($event)"
               (selected_page)="jsonBuilderHelper.setSelectedPage($event)"
               [JSON_Template]="JSON_Template"
               [devMode]="true"
            >
            </inline-temp>
           
        </div>
      `,
  providers: [AnalyticService, TemplateHttpService, TemplateRendererService, RecommendationService]
})

export class TemplateDev implements OnInit {
  Temp_name: String;
  JSON_Template: App;

  constructor(private jsonBuilderHelper: JSONBuilder, private _script: Script, private themingService: ThemingService) {
    //Load Font URL
    let fontURLTag = document.createElement('link');
    fontURLTag.rel = 'stylesheet';
    fontURLTag.type = 'text/css';
    fontURLTag.id = 'fontURL';
    document.getElementsByTagName('head')[0].appendChild(fontURLTag);
    //Load Custom Colors
    let styleTag = document.createElement('style');
    styleTag.type = 'text/css';
    styleTag.id = 'theming';
    document.getElementsByTagName('head')[0].appendChild(styleTag);
    //Load Custom Fonts
    let fontStyles = document.createElement('style');
    fontStyles.type = 'text/css';
    fontStyles.id = 'fontStyles';
    document.getElementsByTagName('head')[0].appendChild(fontStyles);
  }

  ngOnInit() {
    this.JSON_Template = this.jsonBuilderHelper.getJSONBuilt();
    this._script.load('selectize', 'rangeSlider')
      .then((data) => {
        console.log('Scripts Loaded', data);
        this.Temp_name = this.JSON_Template.template;
        this.themingService.seInitialColors();
      })
      .catch((error) => {
        //any error
      });
  }
}




