import { NgModule } from '@angular/core';
import {InlineTempComponent} from './inline_temp.component';
import {TemplateValidatorService} from '../../services/templateValidator.service';
import {SharedModule} from '../../../../shared/modules/shared.module';
import {ControlsModule} from '../../controls/controls.module';

@NgModule({
  declarations: [InlineTempComponent],
  exports: [InlineTempComponent],
  imports: [SharedModule, ControlsModule],
  providers: [TemplateValidatorService]

})
export class InlineTempModule { }




