import { NgModule } from '@angular/core';
import { SharedModule } from '../../../shared/modules/shared.module';
import { TemplateRendererService } from '../services/templateRenderer.service';
import { TemplateHttpService } from '../services/templateHttp.service';
import { AnalyticService } from '../services/analytic.service';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { Template } from './template.component';
import { TEMPLATEMODULES } from './templates';
import { TemplateDev } from './templateDev.component';
import { ThemingService } from '../services/theming.service';
import {ShareOutcomeService} from '../../+builder/services/shareOutcome.service';

@NgModule({
  declarations: [Template, TemplateDev],
  imports: [SharedModule, TEMPLATEMODULES],
  exports: [Template, TemplateDev],
  providers: [JSONBuilder, AnalyticService, TemplateHttpService, TemplateRendererService, ThemingService, ShareOutcomeService],
})

export class TemplateModule {
}



