import { Injectable } from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { THEMING_STYLES, THEMING_PALLETES, THEMING_FONTS, FONT_STYLES } from './theming.store';

declare var jQuery: any;

@Injectable()
export class ThemingService {


  constructor(private _jsonBuilderService: JSONBuilder) {
    //code
  }

  seInitialColors() {
    let color: any = this._jsonBuilderService.getJSONBuilt().theme;
    if ((this.isEmpty(color)) || (color.componentColor === "" && color.bgColor === "" && color.textColor === "" && color.fontFamily === "" && color.fontURL === "" && color.tintRGB === "" && color.tintColor === "")) {
      let neededPallete: any = THEMING_PALLETES.filter((tp) => tp.template == this._jsonBuilderService.getJSONBuilt().template);
      this._jsonBuilderService.getJSONBuilt().theme.fontFamily = neededPallete[0].fontFamily;
      this._jsonBuilderService.getJSONBuilt().theme.fontURL = neededPallete[0].fontURL;
      this._jsonBuilderService.getJSONBuilt().theme.tint = neededPallete[0].tint;
      this._jsonBuilderService.getJSONBuilt().theme.tintRGB = neededPallete[0].tintRGB;
      this._jsonBuilderService.getJSONBuilt().theme.tintColor = neededPallete[0].tintColor;
      for (let pallete of neededPallete[0].pallete) {
        if (pallete.themeClass == this._jsonBuilderService.getJSONBuilt().themeColor) {
          this._jsonBuilderService.getJSONBuilt().theme.componentColor = pallete.components;
          this._jsonBuilderService.getJSONBuilt().theme.bgColor = pallete.backGround;
          this._jsonBuilderService.getJSONBuilt().theme.textColor = pallete.text;
          this._jsonBuilderService.getJSONBuilt().customColor.componentColor = '#FFFFFF';
          this._jsonBuilderService.getJSONBuilt().customColor.bgColor = '#FFFFFF';
          this._jsonBuilderService.getJSONBuilt().customColor.textColor = '#FFFFFF';
        }
      }
    }
    this.setColors();
    this.setFonts();
  }

  setColors() {
    let color: any = this._jsonBuilderService.getJSONBuilt().theme;
    let style = THEMING_STYLES[this._jsonBuilderService.getJSONBuilt().template.replace(/-/g, '_')]
      .replace(/(%[\w]+%)/g, (match) => {
        let replacingColor = match.split(/[%%]/)[1];
        return color[replacingColor] || replacingColor;
      });
    jQuery('#theming').html(style);
  }

  setFonts() {
    let color: any = this._jsonBuilderService.getJSONBuilt().theme;
    let style = FONT_STYLES[this._jsonBuilderService.getJSONBuilt().template.replace(/-/g, '_')]
      .replace(/(%[\w]+%)/g, (match) => {
        let replacingColor = match.split(/[%%]/)[1];
        return color[replacingColor] || replacingColor;
      });
    jQuery('#fontURL').attr('href', color.fontURL);
    jQuery('#fontStyles').html(style);
  }

  isEmpty(obj: any) {
    if (obj == null || obj.length === 0 || typeof obj !== "object") return true;

    if (obj.length > 0) return false;

    for (var key in obj)
      if (key != '_id' && obj[key] != "") return false;

    return true;
  }
}



