import { ShareOutcomeService } from './../../../+builder/services/shareOutcome.service';
import { Component, Input, Output, EventEmitter, OnInit, AfterViewInit } from '@angular/core';
import { Page, Section } from './../../../+builder/models/model';
import { ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { TemplateValidatorService } from '../../services/templateValidator.service';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
import { TemplateHttpService } from '../../services/templateHttp.service';
import { Result } from '../../components/result.directive';
import { AnalyticService } from '../../../templates/services/analytic.service';
import { TemplateRendererService } from '../../services/templateRenderer.service';
import { RecommendationService } from '../../services/recommendation.service';
declare var jQuery: any;
declare var ga: Function;


@Component({
  selector: 'one-page-slider',
  templateUrl: './templatesHtml/one_page_slider.template.html',
  styleUrls: [
    './templatesHtml/assets/css/bootstrap-datepicker.min.css',
    './templatesHtml/assets/css/selectize.default.css',
    './templatesHtml/assets/css/bootstrap-slider.min.css',
    './templatesHtml/assets/css/fonts.css',
    './templatesHtml/assets/css/style1.css',
    './templatesHtml/assets/css/responsive.css'
  ],
  encapsulation: ViewEncapsulation.None
})

export class OnePageSliderComponent implements OnInit, AfterViewInit {
  @Output() selected_control = new EventEmitter();
  @Output() selected_section = new EventEmitter();
  @Output() selected_page = new EventEmitter();
  @Input() JSON_Template: any;
  @Input() devMode: boolean = false;
  staticControls: any = {};
  private formulaResult: any = {};
  private forms: FormGroup[];
  private validated: boolean = false;
  private buttonShowHide: boolean = false;

  constructor(private tvs: TemplateValidatorService,
    private jsonBuilderHelper: JSONBuilder,
    private _analyticService: AnalyticService,
    private _templateHttpService: TemplateHttpService,
    private _templateRenderer: TemplateRendererService,
    private recommendationService: RecommendationService,
    private _outcomeService: ShareOutcomeService

  ) {
    if (!_outcomeService.getSelectedFormula() && jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation')
      recommendationService.getRecomendedResult();
    //code
  }

  ngOnInit() {
    /* static controls */
    this.staticControls = this._templateRenderer.getStaticControls();
  }

  ngAfterViewInit() {

    if (this.jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation') {
      // code
    }

  }
  scrollToTopProperties() {
    setTimeout(function () { jQuery('.details-area').trigger('click'); }, 200);
  }
  onResultLeadFormSubmit(event: any) {
    if (this.jsonBuilderHelper.getJSONBuilt().status == 'LIVE') {

      this.sendCTAAnalytics(event, 'Result');

      // var navigate_url = this.jsonBuilderHelper.getJSONBuilt().navigate_Url;
      // if(control)
      //   this.sendMail().subscribe(
      //     (success: any) => {
      //       // if (event) {
      //       //   if (navigate_url.toLowerCase().indexOf('https://') !== 0 && navigate_url.toLowerCase().indexOf('http://') !== 0) {
      //       //     navigate_url = 'https://' + navigate_url;
      //       //   }
      //       //   window.location.href = navigate_url;

      //       // }
      //     },
      //     (error: any) => {
      //       // if (event) {
      //       //   if (navigate_url.toLowerCase().indexOf('https://') !== 0 && navigate_url.toLowerCase().indexOf('http://') !== 0) {
      //       //     navigate_url = 'https://' + navigate_url;
      //       //   }
      //       //   window.location.href = navigate_url;
      //       // }
      //     }
      //   );
    }
  }
  onLeadFormSubmit(result: boolean, show: string, hide: string) {

    this.sendCTAAnalytics(result, 'Landing');

    this.validated = result;
    if (this.validated)
      this.onButtonClick(null, show, hide);
  }

  onButtonClick(event: any, show: string, hide: string) {
    if (this.validated && !this.devMode) {
      //hide button
      if (event) {
        jQuery(event.target).addClass('hide');
      }

      // if(show.indexOf('page_2')!=-1)
      //   this.sendMail().subscribe((success: any) => {}, (error: any) => {});

      //show next div
      var divToShow = jQuery('.' + show);
      divToShow.removeClass('hide');

      var iOS = (navigator.userAgent.match(/(iPad|iPhone|iPod)/g) ? true : false);
      if (iOS) {
        jQuery('html, body', parent.document).animate(
          {
            scrollTop: divToShow.offset().top
          },
          1000,
          () => {
            if (hide) {
              jQuery('.' + hide).addClass('hide');
              window.scrollTo(0, divToShow.offset().top);
            }
          }
        );
      } else {
        jQuery('html, body').animate(
          {
            scrollTop: divToShow.offset().top
          },
          1000,
          () => {
            if (hide) {
              jQuery('.' + hide).addClass('hide');
              jQuery(window).scrollTop(divToShow.offset().top);
            }
          }
        );
      }
      //make validator available again
      this.validated = false;
      if (this.jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation')
        this.recommendationService.getRecomendedResult();
    }
  }
  getVisiblePages(): any[] {
    return this.JSON_Template.pages.filter((page: any) => page.visible);
  }
  getVisibleSections(page: Page): any[] {
    return page.sections.filter((section: any) => (section.visible && page.type != 'Result'));
  }
  getVisibleItems(section: Section): any[] {
    return section.items.filter((item: any) => item.visible);
  }
  onSubmit(form: FormGroup, sectionId: any) {
    if (form.valid) {
      this.validated = true;

      if (sectionId && this._analyticService.getVisitorKey() != '' && this.jsonBuilderHelper.getJSONBuilt().status == 'LIVE') {
        this._analyticService.saveSectionResult(sectionId)
          .subscribe(
          (response: any) => {
            if (response.status == 'Array Updated')
              this._analyticService.reInitVisitorAnswers();
          },
          (error: any) => {
            console.log(error);
          }
          );
      }
    } else {
      for (let i in form.controls) {
        form.controls[i].markAsTouched();
        form.controls[i].markAsDirty();
      }
      this.validated = false;
    }
  }
  /*  ---
   when control is selected from particluar template then it pass to parent
   template component (i.e Template.ts) and then parent template component pass to home component
   */
  selectControl(control: any) {
    this.selected_control.emit(control);
  }

  selectSection(section: any) {
    this.selected_section.emit(section);
  }

  selectpage(page: any) {
    this.selected_page.emit(page);
  }

  selectModel(event: any, type: any) {
    this.jsonBuilderHelper.setSelectedModel(type);
    event.stopPropagation();
  }

  /** Method addded for Sending email **/
  sendMail() {
    let data: any = {
      calcId: this.jsonBuilderHelper.getJSONBuilt()._id,
      visitorId: this._analyticService.getVisitorKey(),
      type: 'Finish'
    };
    return this._templateHttpService.sendEmail(data);
  }

  sendCTAAnalytics(isCTA: any, page: string) {
    if (isCTA != true && isCTA != false) {
      //Ours
      ga('devteam.send', 'event', this.jsonBuilderHelper.getJSONBuilt().name + ' ' + page + ' Page CTA', 'Clicked', this.jsonBuilderHelper.getJSONBuilt().url);
      //Users
      if (this.jsonBuilderHelper.getJSONBuilt().ga)
        ga('userCustom.send', 'event', this.jsonBuilderHelper.getJSONBuilt().name + ' ' + page + ' Page CTA', 'Clicked', this.jsonBuilderHelper.getJSONBuilt().url);
    }
  }
}



